body {
    background-color: #000;
    color: #fff;
    min-height: 100vh;
    display: grid;
    place-items: center;
    perspective: 500px;
  }
  
  .banner {
    display: flex;
  }
  
  .panel {
    cursor: pointer;
    position: relative;
    width: 21px;
    height: 120px;
    overflow: hidden;
    animation: rotate 6s var(--delay) infinite ease-in-out alternate;
  }
  
  @keyframes rotate {
    0%, 10% {
      transform: rotateX(0deg);
    }
    90%, 100% {
      transform: rotateX(720deg);
    }
  }
  
  .panel::before {
    position: absolute;
    left: var(--left);
    content: 'Diamond';
    font-size: 110px;
    width: max-content;
    color: hsl(var(--hue), 75%, 75%);


  }

  @media (max-width: 480px){
    .panel::before{
        font-size: 80px;
        margin: 0px 100px ;
    }
}
  
  .panel:nth-child(1) {
    --left: 0px;
    --hue: 0;
    --delay: 0s;
  }
  
